<template>
  <div class="end-fight">
    <!-- who -->
    <div @click="goToPre" v-if="state==='who'" class="fight-pre-box">
      <div class="fight-who-bg"></div>
      <img class="fight-who-gg" src="@/assets/images/ending/ending-gg-black.png" alt="fight-word">
      <div class="fight-pre-text">你突然發現身後多出了一個黑影，<br>正準備回頭看時，一陣怪力向你襲來。<br><br>你心想，難道還有沒被捕捉的怪奇？</div>
    </div>
    <!-- pre -->
    <div v-if="state==='pre'" class="fight-pre-box">
      <div class="fight-pre-bg" :style="{ backgroundImage: 'url(' + endBg + ')' }"></div>
      <div class="fight-pre-ability">關卡怪力值<br><span style="font-size:24px;">6</span></div>
      <img class="fight-pre-word" src="@/assets/images/ling/fight-word.png" alt="fight-word">
      <div class="fight-pre-text">怪奇會施展怪力干擾<br>讓怪力求保持在中間區域即可戰勝</div>
      <div class="fight-pre-btn-box">
        <img @click="backToCompass" class="fight-pre-btn" src="@/assets/images/ling/run-away.png" alt="run-away">
        <img @click="goToFight" class="fight-pre-btn" src="@/assets/images/ling/enter-in.png" alt="enter-in">
      </div>
    </div>
    <!-- ing -->
    <div v-else-if="state==='ing'" class="fight-ing-box">
      <div class="fight-ing-bg" :style="{ backgroundImage: 'url(' + endBg + ')' }"></div>
      <img class="fight-ing-ling-icon" src="@/assets/images/ending/ending-gg-green.png" alt="fight-word">
      <div class="fight-ing-time">{{ time }}</div>
      <div class="fight-ing-bar" :style="{ backgroundImage: 'url(' + ballBar + ')' }">
        <img
          :style="{ marginLeft:  ballPosition + 'px' }"
          class="fight-ing-ball"
          src="@/assets/images/ling/play-ball.png"
          alt="play-ball">
      </div>
      <div class="fight-ing-btn-box">
        <img @click="goLeft" class="fight-ing-btn" src="@/assets/images/ling/go-left.png" alt="go-left">
        <img @click="goRight" class="fight-ing-btn" src="@/assets/images/ling/go-right.png" alt="go-right">
      </div>
    </div>
    <!-- lose -->
    <div @click="goToEndOne" v-else-if="state==='lose'" class="fight-lose-box">
      <div class="fight-lose-bg" :style="{ backgroundImage: 'url(' + endBg + ')' }"></div>
      <img class="fight-lose-ling-icon" src="@/assets/images/ending/ending-gg-green.png" alt="fight-word">
      <div class="fight-lose-ch">殘念！</div>
    </div>
    <!-- win -->
    <!-- <div @click="goToCollect" v-else-if="state==='win'" class="fight-lose-box">
      <div class="fight-lose-bg" :style="{ backgroundImage: 'url(' + endBg + ')' }"></div>
      <img class="fight-lose-ling-icon" src="@/assets/images/river/river-grey-icon.png" alt="fight-word">
      <div style="font-size: 18px; text-align: left;" class="fight-lose-ch">啊......我、我竟然輸了<br>嗚嗚嗚，我果然是最沒用的怪奇<br>我的存在一點利用價值都沒有......</div>
    </div> -->
  </div>
</template>

<script>

import endBg from '@/assets/images/ending/ending-fight-bg.png'
import ballBar from '@/assets/images/ling/vertical-bar.png'
export default {
  name: 'EndFightOne',
  components: {

  },
  data () {
    return {
      endBg: endBg,
      ballBar: ballBar,
      state: 'who',
      time: 15,
      ballPosition: 0
    }
  },
  mounted () {
    this.$store.commit('changeHeader', false)
  },
  methods: {
    backToCompass () {
      this.$store.commit('changeHeader', true)
      this.$router.push({
        path: './monster-compass'
      })
    },
    goToFight () {
      this.state = 'ing'
      this.gameStart()
    },
    gameStart () {
      // 時間開始
      let round1 = setInterval(() => {
        this.time--
      }, 1000)

      setTimeout(() => {
        if (this.ballPosition < -160) {
          this.ballPosition = -250
        } else {
          this.ballPosition = this.ballPosition - 90
        }
      }, 2000)

      setTimeout(() => {
        if (this.ballPosition > 100) {
          this.ballPosition = 250
        } else {
          this.ballPosition = this.ballPosition + 150
        }
      }, 5000)

      setTimeout(() => {
        if (this.ballPosition > 60) {
          this.ballPosition = 250
        } else {
          this.ballPosition = this.ballPosition + 190
        }
      }, 9000)

      setTimeout(() => {
        if (this.ballPosition < -150) {
          this.ballPosition = -250
        } else {
          this.ballPosition = this.ballPosition - 100
        }
      }, 11000)

      setTimeout(() => {
        this.ballPosition = -250
      }, 14800)

      // 時間結束
      setTimeout(() => {
        clearInterval(round1)
        if (this.ballPosition < 150 && this.ballPosition > -150) {
          this.winGame()
        } else {
          alert('你輸了！')
          this.state = 'lose'
        }
      }, 15000)
    },
    winGame () {
      alert('你贏了！')
      // this.$store.commit('changeHeader', true)
      // this.$store.commit('changeNoteDot', true)
      // this.$store.commit('changeFrameDot', true)
      // this.$store.commit('luluWin')
      this.state = 'win'
      // this.$store.commit('getLuluCard', 'grey')
    },
    goLeft () {
      this.ballPosition = this.ballPosition - 10
    },
    goRight () {
      this.ballPosition = this.ballPosition + 10
    },
    goToPre () {
      this.state = 'pre'
    },
    goToCollect () {
      this.$router.push({
        path: './river-house-end'
      })
    },
    goToEndOne () {
      this.$router.push({
        path: './end-talk-one'
      })
    }
  },
  computed: {
    power () {
      return this.$store.state.power
    }
  }
}
</script>
<style lang="scss" scoped>

  .end-fight {
    min-height: 100vh;
    background: linear-gradient(#000 70%, #95ff00);
    touch-action: manipulation;

    .fight-pre {

      &-box {
        min-height: 100vh;
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
      }

      &-bg {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background-repeat: no-repeat;
        background-size: cover;
        background-position-x: center;
        background-position-y: center;
        z-index: 0;
      }

      &-ability {
        position: absolute;
        top: 15px;
        right: 20px;
        padding: 4px;
        color: black;
        background-color: #95ff00;
        border-radius: 4px;
        text-align: center;
        z-index: 1;
      }

      &-word {
        width: 100px;
        height: 100%;
        z-index: 1;
      }

      &-text {
        color: #95ff00;
        font-size: 16px;
        margin-top: 16px;
        text-align: center;
        z-index: 1;
      }

      &-btn-box {
        margin-top: 16px;
        display: flex;
        justify-content: space-around;
        z-index: 1;
      }

      &-btn {
        width: 80px;
        height: 80px;
        margin: 0px 20px;
      }
    }

    .fight-ing {

      &-box {
        min-height: 100vh;
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
      }

      &-bg {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background-repeat: no-repeat;
        background-size: cover;
        background-position-x: center;
        background-position-y: center;
        z-index: 0;
      }

      &-ling-icon {
        position: absolute;
        top: 40px;
        width: 70vw;
        height: auto;
      }

      &-time {
        position: absolute;
        top: 20px;
        left: 20px;
        text-align: center;
        font-size: 30px;
        color: #95ff00;
        z-index: 2;
      }

      &-bar {
        margin-top: 160px;
        display: flex;
        justify-content: center;
        background-repeat: no-repeat;
        background-size: cover;
        background-position-x: center;
        background-position-y: center;
        width: 300px;
        height: 50px;
        border-radius: 40px;
        z-index: 2;
      }

      &-ball {
        width: 50px;
        height: 50px;
      }

      &-btn-box {
        margin-top: 16px;
        display: flex;
        justify-content: space-around;
        width: 300px;
        z-index: 1;
      }

      &-btn {
        width: 80px;
        height: 80px;
      }

    }

    .fight-lose {

      &-box {
        min-height: 100vh;
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
      }

      &-bg {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background-repeat: no-repeat;
        background-size: cover;
        background-position-x: center;
        background-position-y: center;
        z-index: 0;
      }

      &-ling-icon {
        width: 80vw;
        height: auto;
        z-index: 2;
      }

      &-ch {
        position: absolute;
        text-align: center;
        font-size: 52px;
        color: #95ff00;
        padding-left: 34px;
        z-index: 2;
      }

      &-text {
        color: #95ff00;
        text-align: center;
        z-index: 2;
      }
    }

    .fight-who {

      &-bg {
      background: linear-gradient(#06143C 70%, #95ff00);
      position: absolute;
      left: 0;
      top: 0;
      width: 100vw;
      height: 100vh;
      background-repeat: no-repeat;
      background-size: contain;
      z-index: 0;
      }

      &-gg {
        position: absolute;
        top: 12vh;
        width: 24%;
        height: auto;
      }
    }
  }

</style>



